export default function App() {
  const [bsx, setBsx] = useState("");
  const menu = data
    .filter((val) => {
      if (bsx === "") {
        return 0;
      } else if (val.bsx.toLowerCase().includes(bsx.toLowerCase())) {
        return val;
      }
      return 0;
    })
    .map((val) => {
      return (
        <div key={val.bsx} className="card bg-light text-black m-5 p-3">
          <p>
            <img src={val.hinhAnh} alt="Khong co hinh" />
            <i>
              <h4>BSX: {val.bsx}</h4>
            </i>
          </p>
          <div className="card bg-danger text-white p-4 m-4">
            <strong>Số phòng: {val.soPhong}</strong>
          </div>
        </div>
      );
    });

  return (
    <div className="App">
      <h1>TÌM KIẾM BSX</h1>
      <input type="text" value={bsx} onChange={(e) => setBsx(e.target.value)} />
      {menu}
    </div>
  );
}
